
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import tools from '@/common/tools.js'
import axios from 'axios'
export default {
name: 'shipaddress',
data: function() {
return {
allData: {},
confirmShow: false,
nowIndex: 0,
fromType: ''
}
},
directives: {
TransferDom
},
activated() {
this._getAddressData()
},
created() {
this.fromType = this.$route.query.type
this._getAddressData()
},
methods: {
selectAddress(index) {
if (this.fromType == 'confirmorder') {
this.$store.commit('SET_ADDRESSDATA', this.allData[index])
console.log(this.allData[index].address_id)
this.$router.go(-1)
}
},
onConfirm() {
console.log('我要开始删除了')
console.log('delIdArr值是', this.delIdArr)

let url = '/Mobile/User/del_address.html'
axios.get(url, {
params: {
address_id: this.allData[this.nowIndex].address_id
}
})
.then((res) => {
console.log(res)
if (res.data.status == 1) {
this.allData.splice(this.nowIndex, 1)
} else {
tools.showError(res.data.msg)
}
})
.catch((error) => {
console.log(error)
})
},
setDefaultAddress(index) {
console.log('设置默认地址', index)
if (this.allData[index].is_default == 1) {
this.allData[index].is_default = 1
return
} else {

let url = '/Mobile/User/set_default.html'
axios.get(url, {
params: {
address_id: this.allData[index].address_id
}
})
.then((res) => {
console.log(res)
if (res.data.status == 1) {
for (let i = 0; i < this.allData.length; i++) {
this.allData[i].is_default = 0
}
this.allData[index].is_default = 1
} else {
tools.showError(res.data.msg)
}
})
.catch((error) => {
console.log(error)
})
}
},
editAddress(index) {
console.log('编辑', index)
this.$router.push({path: 'editaddress', query: {addressId: this.allData[index].address_id}})
},
delAddress(index) {
console.log('删除', index)
this.nowIndex = index
this.confirmShow = true
},
_getAddressData() {
let url = '/Mobile/User/address_list.html'
axios.get(url, {
params: {}
})
.then((res) => {
console.log(res)
if (res.data.status == 1) {
this.allData = res.data.data
} else {
tools.showError(res.data.msg)
}
})
.catch((error) => {
console.log(error)
})
}
},
components: {
Confirm
}
}



